import React, { useState } from 'react';
import { Link, Redirect } from 'react-router-dom';
import './LogIn.css';
import { connect } from 'react-redux'
import { login } from '../actions/auth'

function Body({ login, isAuthenticated }) {
    const [formData, setFormData] = useState({
        email: '',
        password: ''
    });

    const { email, password } = formData;

    const onChange = e => setFormData({...formData, [e.target.name]: e.target.value });

    const onSubmit = e => {
        e.preventDefault();

        login(email, password);
    };

    // is user authenticated -> redirect to home page
    if (isAuthenticated) {
        return <Redirect to='/' />
    }

    return (
        <div class="log-in-body">
            <div className="join-form">
                <form onSubmit={ e => onSubmit(e) }>
                    <div class="input-group">
                        <label>Email:</label>
                        <input type="email" 
                               placeholder='email'
                               name="email" 
                               value={email} 
                               onChange={e => onChange(e)} 
                               required
                        />
                    </div>
                    <div class="input-group">
                        <label>Password:</label>
                        <input type="password" 
                               placeholder='password'
                               name='password' 
                               value={password} 
                               onChange={e => onChange(e)} 
                               required
                        />
                    </div>
    
                    <div class="password-reset">
                        <Link to='/reset_password' class="pwordreset">Password Reset</Link>
                    </div>
                    <div class="input-group">
                        <button type="submit" class="button">Log In</button>
                    </div>
                </form>
            </div>
            <div class="join-now-message">
                <span class="join-now">Not already a registered user? </span>
                <Link to='/signup' className="signup-link">Sign Up</Link>
                <span class="join-now"> with us today!</span>
            </div>
            
        </div>);
};

const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated
});

export default connect(mapStateToProps, { login })(Body);