import React from 'react';
import './App.css';
import Home from './components/pages/Home';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import MLAlgorithm from './components/pages/MLAlgorithm';
import Products from './components/pages/Products';
import SignUp from './components/pages/SignUp';
import SignUpProper from './components/pages/SignUpProper';
import Pwordreset from './components/pages/Pwordreset';
import contactussuccess from './components/pages/Contactussuccess';
import compare from './components/pages/Compare';
import ResetPasswordConfirm from './components/pages/ResetPasswordConfirm';

import { Provider } from 'react-redux';
import store from './store';
import Layout from './hocs/Layout';


function App() {
  return (
    <Provider store={store}>
      <Router>
        <Layout>
          <Switch>
            <Route exact path='/' component={Home} />
            <Route exact path='/MLAlgorithm' component={MLAlgorithm} />
            <Route exact path='/products' component={Products} />
            <Route exact path='/login' component={SignUp} /> 
            <Route exact path='/signup' component={SignUpProper} />
            <Route exact path='/reset_password' component={Pwordreset} />
            <Route exact path='/contactsuccess' component={contactussuccess} />
            <Route exact path='/compare' component={compare} />
            <Route exact path='/password/reset/confirm/:uid/:token' component={ResetPasswordConfirm} />
          </Switch>
        </Layout>
      </Router>
    </Provider>
  );
}

export default App;
