import React, { useState } from 'react';
import { Link, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { reset_password } from '../actions/auth';
import './Pwordresetcomponent.css';

const ResetPassword = ({ reset_password }) => {
    const [requestSent, setRequestSent] = useState(false);
    const [formData, setFormData] = useState({
        email: ''
    });

    const { email } = formData;

    const onChange = e => setFormData({...formData, [e.target.name]: e.target.value });

    const onSubmit = e => {
        e.preventDefault();

        reset_password(email);
        setRequestSent(true);
    };

    // is user authenticated -> redirect to home page
    if (requestSent) {
        return <Redirect to='/login' />
    }


    return (
        <div className="pwordreset-body">
            <div className='pwordreset-message'>Password Reset</div>
            <div className="pwordreset-form">
                <form onSubmit={e => onSubmit(e)}>
                    <div className="input-group">
                        <label>Email:</label>
                        <input 
                            type="email"
                            placeholder='email'
                            name='email'
                            value={email}
                            onChange={e => onChange(e)}
                            required
                        />
                    </div>
                    
                    <div class="input-group">
                        <button type="submit" class="button">Request New Password</button>
                    </div>
                </form>
            </div>
        </div>);}

export default connect(null, { reset_password })(ResetPassword);
