import React, { useState } from 'react';
import { Link, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { reset_password_confirm } from '../actions/auth';
import './Pwordresetcomponent.css';

const ResetPasswordConfirmComponent = ({ match, reset_password_confirm }) => {
    const [requestSent, setRequestSent] = useState(false);
    const [formData, setFormData] = useState({
        new_password: '',
        re_new_password: ''
    });

    const { new_password, re_new_password } = formData;

    const onChange = e => setFormData({...formData, [e.target.name]: e.target.value });

    const onSubmit = e => {
        e.preventDefault();

        const uid = match.params.uid;
        const token = match.params.token;

        reset_password_confirm(uid, token, new_password, re_new_password);
        setRequestSent(true);
    };

    // is user authenticated -> redirect to home page
    if (requestSent) {
        return <Redirect to='/' />
    }


    return (
        <div className="pwordreset-body">
            <div className='pwordreset-message'>Password Reset</div>
            <div className="pwordreset-form">
                <form onSubmit={e => onSubmit(e)}>
                    <div className="input-group">
                        <label>New Password:</label>
                        <input 
                            type="password"
                            placeholder='New Password'
                            name='new_password'
                            value={new_password}
                            onChange={e => onChange(e)}
                            required
                        />
                    </div>
                    <div className="input-group">
                        <label>Re-Enter New Password:</label>
                        <input 
                            type="password"
                            placeholder='Re-Enter New Password'
                            name='re_new_password'
                            value={re_new_password}
                            onChange={e => onChange(e)}
                            required
                        />
                    </div>
                    <div class="input-group">
                        <button type="submit" class="button">Confirm Password Reset</button>
                    </div>
                </form>
            </div>
        </div>);}

export default connect(null, { reset_password_confirm })(ResetPasswordConfirmComponent);
